<template>
  <el-table
    :data="list"
    style="width: 100%"
    stripe
    border
  >
    <el-table-column
      type="index"
      width="100"
      label="序号"
      fixed
      :index="(index) => index + 1"
    />
    <el-table-column
      v-for="info in tableCols"
      :key="info.label"
      :label="info.label"
      :align="info.align || 'center'"
    >
      <template v-if="info.children">
        <el-table-column
          v-for="item in info.children"
          :key="item.prop"
          :column-key="item.prop"
          header-align="center"
          :align="item.align"
          :prop="item.prop"
          :label="item.label"
          :width="item.width"
        >
          <template slot-scope="scope">
            <el-tooltip
              :disabled="!item.openTooltip"
              class="item"
              effect="dark"
              :content="getValue(scope.row, item.prop)"
              placement="top-start"
            >
              <p class="u-line-2">{{ getValue(scope.row, item.prop) }}</p>
            </el-tooltip>
          </template>
        </el-table-column>
      </template>

    </el-table-column>
  </el-table>
</template>

<script>
export default {
  name: 'ComplexTable',
  props: {
    list: {
      type: Array,
      default: () => []
    },
    tableCols: {
      type: Array,
      default: () => []
    }
  },
  methods: {
    // 下载
    download() {
      import('@/vendor/Export2Excel').then(excel => {
        const tHeader = []
        this.tableCols.forEach(item => {
          if (item.children) {
            item.children.forEach(item2 => {
              tHeader.push(item2.label)
            })
          } else {
            tHeader.push(item.label)
          }
        })
        const filterVal = []
        this.tableCols.forEach(item => {
          if (item.children) {
            item.children.forEach(item2 => {
              filterVal.push(item2.prop)
            })
          } else {
            filterVal.push(item.prop)
          }
        })
        const data = this.formatJson(filterVal)
        excel.export_json_to_excel({
          header: tHeader,
          data,
          filename: 'table-list'
        })
        this.downloadLoading = false
        setTimeout(() => {
          this.$emit('download-complete', true)
        }, 1000)
      })
    },
    // 格式化excel表格
    formatJson(filterVal) {
      return this.list.map(v => filterVal.map(j => {
        if (j === 'timestamp') {
          return 'timestamp'
        } else {
          return this.getValue(v, j)
        }
      }))
    },
    // 获取对象值 多级
    getValue(obj, key) {
      let curVal = obj
      const keys = key.split('.')
      keys.forEach(key => {
        curVal = curVal[key]
      })
      return String(curVal)
    }
  }
}
</script>

