<template>
  <div class="page-box">
    <div>
      <div class="table-tools">
        <el-button
          type="primary"
          @click="clickImport"
        >导入</el-button>
        <el-button
          type="primary"
          :disabled="downloadLoading"
          :loading="downloadLoading"
          @click="handleDownload"
        >下载</el-button>
      </div>
      <complex-table
        ref="complexTable"
        :list="list"
        :table-cols="tableCols"
        @download-complete="downloadLoading = false"
      />
    </div>
    <!--    导入数据dia-->
    <el-dialog
      title="导入数据"
      :visible.sync="dialogVisible"
      width="500px"
      top="5vh"
      append-to-body
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :show-close="false"
      :before-close="handleClose"
    >
      <div class="textarea-list">
        <div
          v-for="(item, index) in importList"
          :key="index"
          class="textarea-item"
        >
          <div class="index-name">{{ index + 1 }}：</div>
          <el-input
            v-model="item.textarea"
            type="textarea"
            placeholder="输入(full)Response复制的数据"
            :autosize="{ minRows: 2, maxRows: 2}"
            clearable
          />
        </div>

      </div>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="handleClose">取 消</el-button>
        <el-button
          type="primary"
          @click="confirmImport"
        >导 入</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import ComplexTable from '@/components/complex-table/index'
export default {
  name: 'HomePage',
  components: {
    ComplexTable
  },
  data() {
    return {
      downloadLoading: false,
      dialogVisible: false,
      importList: [],
      importLength: 20,
      cloneImportList: [],
      list: [],
      tableCols: [
        {
          label: '视频信息',
          align: 'center',
          children: [
            {
              label: '视频id',
              prop: 'id',
              align: 'left',
              width: '180'
            },
            {
              label: '视频播放地址',
              prop: 'videoPlayUrl',
              align: 'left',
              width: '180'
            },
            {
              label: '视频描述',
              prop: 'desc',
              align: 'left',
              width: '220',
              openTooltip: true
            },
            {
              label: '视频播放数',
              prop: 'stats.playCount',
              align: 'right',
              width: '120'
            },
            {
              label: '视频收藏数',
              prop: 'stats.collectCount',
              align: 'right',
              width: '120'
            },
            {
              label: '视频评论数',
              prop: 'stats.commentCount',
              align: 'right',
              width: '120'
            }
          ]
        }, {
          label: '博主信息',
          align: 'center',
          children: [
            {
              label: '博主ID',
              prop: 'author.uniqueId',
              align: 'left',
              width: '180'
            },
            {
              label: '博主昵称',
              prop: 'author.nickname',
              align: 'left',
              width: '180'
            },
            {
              label: '博主主页',
              prop: 'authorUrl',
              align: 'left',
              width: '180'
            },
            {
              label: '博主粉丝数',
              prop: 'authorStats.followerCount',
              align: 'right',
              width: '120'
            },
            {
              label: '博主总点赞数',
              prop: 'authorStats.heartCount',
              align: 'right',
              width: '120'
            },
            {
              label: '博主视频数',
              prop: 'authorStats.videoCount',
              align: 'right',
              width: '120'
            },
            {
              label: '博主签名',
              prop: 'author.signature',
              align: 'left',
              width: '180',
              openTooltip: true
            },
            {
              label: '邮箱',
              prop: 'email',
              align: 'left',
              width: '150'
            }
          ]
        }
      ]
    }
  },
  created() {
    // 初始化导入
    for (let i = 0; i < this.importLength; i++) {
      this.importList.push({
        textarea: ''
      })
    }
    this.cloneImportList = JSON.parse(JSON.stringify(this.importList))
  },
  methods: {
    // 导入
    clickImport() {
      this.importList = JSON.parse(JSON.stringify(this.cloneImportList))
      this.dialogVisible = true
    },
    // 关闭导入
    handleClose() {
      this.dialogVisible = false
    },
    // 确认导入
    confirmImport() {
      try {
        let list = []
        this.importList.forEach(item => {
          const str = item.textarea.trim()
          if (str) {
            list = list.concat(JSON.parse(str).item_list)
          }
        })
        this.list = this.formListData(list)
        this.handleClose()
      } catch (e) {
        console.log(e)
        this.$message.error('数据错误')
      }
    },
    // 点击下载
    handleDownload() {
      this.downloadLoading = true
      this.$refs.complexTable.download()
    },
    // 格式化列表
    formListData(list) {
      return list.map(item => {
        return {
          ...item,
          email: this.$utils.getMailByText(item.author.signature),
          authorUrl: `https://www.tiktok.com/@${item.author.uniqueId}`,
          videoPlayUrl: `https://www.tiktok.com/@${item.author.uniqueId}/video/${item.id}`
        }
      })
    }

  }
}
</script>

