const PrivateClass = {
  /**
   * 提取文本中的邮箱地址
   * @param text {string}文本
   * @returns {string} 邮箱地址
   */
  getMailByText: (text) => {
    const emailRegex = /\b[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Z|a-z]{2,}\b/g
    const mailArr = text.match(emailRegex) || []
    return mailArr.join('，')
  }
}
export default {
  getMailByText: PrivateClass.getMailByText
}
